/* Height width of keys is set with flexbox */
.Key {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex: 1 0 25%;
      flex: 1 0 25%;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  border-style: solid;
  border-left-width: 0;
  border-top-width: 0;
  border-right-width: 1px;
  border-bottom-width: 1px;
  background: #e0e0e7;
  border-color: #aaa;
  font-size: 22px;
  font-weight: 300;
  -webkit-transition: all 100ms;
  -o-transition: all 100ms;
  transition: all 100ms;
}

.Key:first-of-type {
  border-left-width: 1px !important;
}

/* Keys with special styles
**************************************************/
#zero {
  -ms-flex: 2 0 50%;
      flex: 2 0 50%; /* The zero is twice the width of other keys */
}
.Key:last-of-type {
  /* The symbols for the operation keys render smaller than regular characters
 so they have a larger font size to make them look more equal */
  font-size: 27px;
}

#percent,
#neg,
#clear {
  /* These characters look slightly oversized, so they have a smaller font size */
  font-size: 20px;
}

.Key:last-of-type {
  background: #f77e1b;
  color: #fff;
}

.Key-label {
}

/* Active state for regular keys */
.Key:active,
.Key.active {
  background: #d8d8df;
}

.Key:last-of-type:active,
.Key:last-of-type.active {
  background: #ff8f39;
}



