.Display {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex: 1 1;
      flex: 1 1;
  background: -webkit-linear-gradient(45deg, #504b4b, #5e5757);
  background: -o-linear-gradient(45deg, #504b4b, #5e5757);
  background: linear-gradient(45deg, #504b4b, #5e5757);
  border: solid 1px #504b4b;
  border-bottom-width: 0;
  font-size: 36px;
  font-weight: 300;
  color: #f1f1f1;
}

@media screen and (min-width: 400px) {
  .Display {
    border-radius: 8px 8px 0 0;
  }
}

.Display-value {
  margin: 10px 20px 10px 10px;
  font-size: 100%;
}



