/* The keypad height is 80% of total height of the calculator.
This allows the hight of the calculator to scale on smaller screens. */
.Keypad {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 2.5 1;
      flex: 2.5 1;
}

.Keypad-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 20%;
      flex: 1 0 20%;
}



